body {
  background-color: var(--color-theme-bg);
  display: grid;
  grid-template-rows: 1fr min-content;
  align-items: center;
  justify-items: center;
}

#background {
  height: 100%;
  width: 100%;
  z-index: -1;
  position: fixed;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_16___});
  background-repeat: no-repeat;
  background-size: cover;
}

.window {
  margin-top: 3rem;
  width: clamp(85ch, 60%, 150ch);
  display: grid;
  row-gap: 1rem;
  column-gap: 1rem;

  > * {
    background-color: var(--color-theme-main);
    padding: 1rem;
    border-radius: 1rem;
  }
}

.dashboard {
  display: grid;
  justify-items: center;
  gap: 0.5rem;

  .weather-icon {
    width: 128px;
    height: 128px;
    display: flex;
  }
}

@media (max-width: 800px) {
  #window {
    width: 100%;

    > * {
      border-radius: 0;
    }
  }
}

.loader {
  border: 1rem solid var(--color-theme-bg);
  border-top: 1rem solid var(--color-theme-highlight);
  border-radius: 50%;

  animation: spin 1.5s linear infinite;

  * {
    display: none;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.search {
  position: relative;
  border-bottom: 1px solid var(--color-theme-border);

  &:focus-within {
    .results {
      background-color: var(--color-theme-focus-bg);
    }

    & .input,
    .results {
      outline: 1px solid var(--color-theme-focus);
    }

    & * {
      &.selected {
        background-color: var(--color-theme-highlight-bg);
      }
    }
  }

  &:not(:focus-within) {
    .results {
      display: none;
    }
  }

  .buttons {
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;

    display: grid;
    grid-auto-flow: column;
    column-gap: 0.5rem;

    button {
      width: 2rem;
      height: 2rem;
      padding: 0.2rem;

      border: 1px solid var(--color-theme-border);
      border-radius: 0.2rem;
    }

    button:hover,
    button:active {
      background-color: var(--color-theme-highlight-bg);
    }
  }

  &.locked {
    background-color: none;
    border: none;
  }

  .input {
    width: 100%;
    height: 5rem;
    font-size: 3rem;
    border-radius: inherit;
    padding: 0.5rem;
    text-align: center;
  }

  .results {
    position: absolute;
    z-index: 1;
    background-color: inherit;
    width: 100%;
    display: grid;
    max-height: 12rem;
    overflow-y: scroll;

    &:not(:has(*)) {
      display: none;
    }

    > *:not(:first-child) {
      border-top: 1px solid var(--color-theme-border);
    }

    .option {
      &:hover,
      &:active {
        background-color: var(--color-theme-highlight-bg);
      }
      button {
        border: none;
        background-color: inherit;

        width: 100%;
        height: 100%;
        padding: 1rem;

        display: flex;
        justify-content: left;
      }
    }
  }
}

@media (max-width: 800px) {
  .search {
    .input {
      font-size: 2.5rem;
    }
    .buttons {
      top: -2.5rem;
    }
  }
}

@media (max-width: 500px) {
  .search {
    .input {
      font-size: 2rem;
    }
  }
}

.temperature {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 1rem;

  .value {
    font-size: 4rem;
  }

  .units {
    position: absolute;
    right: -3rem;
    top: 0.3rem;

    display: grid;
    font-size: 1.2rem;
    color: var(--color-theme-text-alt);

    .active {
      font-size: 2rem;
      color: var(--color-theme-text-main);
    }

    button {
      border: none;
    }
  }
}

.weather-info {
  display: grid;
  grid-template-columns: max-content max-content;
  column-gap: 2rem;
  row-gap: 1rem;

  .item {
    display: flex;
    gap: 0.5rem;
    align-items: center;
    font-size: 1.5rem;

    &.condition,
    &.feels-like {
      grid-column: 1 / -1;
      justify-self: center;
      font-size: 2rem;
    }

    .content {
      font-weight: bold;
    }
  }

  .icon-container {
    flex-shrink: 0;
    width: 3rem;
    height: 3rem;
    padding: 0.2rem;
  }
}

@media (max-width: 800px) {
  .weather-info {
    grid-template-columns: 1fr;

    .item {
      justify-self: center;

      &.condition,
      &.feels-like {
        font-size: 1.5rem;
      }
    }
  }
}

@media (max-width: 500px) {
  .weather-info {
    grid-template-columns: 1fr;
    justify-items: center;

    .item {
      justify-self: left;

      &.condition,
      &.feels-like {
        justify-self: left;
        font-size: 1.5rem;
      }
    }
  }
}

.forecast {
  display: grid;
  row-gap: 1rem;

  button.expand {
    border: none;
    background-color: initial;
    display: flex;
    justify-content: center;
    gap: 2rem;
    flex-direction: row-reverse;
    cursor: initial;

    .icon {
      display: none;
      width: min-content;
      aspect-ratio: 1;
    }
  }

  .forecast-heading {
    font-size: 1.5rem;
    font-weight: bold;
    justify-self: center;
  }
}

.forecast-container {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  overflow: scroll;
}

.forecast-unit {
  border: none;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 0.5rem;

  .date {
    align-self: center;
    font-weight: bold;
  }

  &:not(:focus-within) {
    .item {
      &.rain-chance,
      &.snow-chance {
        display: none;
      }
      .label {
        display: none;
      }
    }
  }
}

.forecast-info {
  flex-grow: 1;
  background-color: var(--color-theme-alt);
  padding: 0.5rem;
  border: 2px solid var(--color-theme-border);
  display: grid;
  grid-template-columns: min-content 1fr;
  justify-items: center;
  align-items: center;
  border-radius: 1rem;

  &.hourly {
    .information {
      flex-wrap: nowrap;
    }
  }

  .information {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    column-gap: 0.5rem;

    > * {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 0.5rem;
    }
    .item {
      display: grid;
      grid-template-columns: 1.5rem 2.2rem;
      grid-template-rows: min-content min-content;
      align-items: center;
      justify-items: center;
      column-gap: 0.2rem;
      .label {
        grid-column: 1 / -1;
        grid-row: 1 / 2;
      }
      .content {
        grid-column: 2;
        font-weight: bold;
        text-align: center;
      }
      .icon-container {
        grid-column: 1 / 2;
      }
    }
  }
  .icon-container {
    flex-shrink: 0;
    width: 1.5rem;
    height: 1.5rem;
  }
}

@media (max-width: 500px) {
  .forecast {
    padding-left: 0;
    padding-right: 0;

    button.expand {
      cursor: pointer;
      .icon {
        display: initial;
      }
    }

    &:not(.expanded) {
      .forecast-container {
        display: none;
      }
    }
  }
  .forecast-container {
    flex-direction: column;
    padding-left: 0;
    padding-right: 0;
  }

  .forecast-unit {
    background-color: var(--color-theme-alt);
    flex-direction: row;
    padding-right: 1rem;
    padding-left: 1rem;
  }

  .forecast-info {
    background-color: initial;
    border: none;
    border-radius: 0;
    grid-template-columns: min-content 1fr;
    align-items: center;

    .information > * {
      flex-wrap: wrap;
    }

    &.hourly {
      .information {
        flex-wrap: wrap;
      }
    }
  }
}

#footer {
  height: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
